.header-pop-mod{
	.header-location-item{
		position:relative;
		em.address{
			height:34px;
			line-height: 34px;
			padding: 0 40px;
			display:inline-block;
			border-radius: 15px;
			background:#fff;
			color: #666;
			position:relative;
			&:before{
				content: "";
				width:16px;
				height:16px;
				display: inline-block;
				background:url(../images/icon-location-16x16.png) no-repeat;
				position:absolute;
				left: 20px;
				top: 50%;
				margin-top: -8px;
			}
			&:after{
				content: "";
				width:0px;
				height:0px;
				font-size:0px;
				line-height:0px;
				border-style:solid;
				position:absolute;
				border-width: 5px 5px  0px 5px;/* 大小 */
				border-color: #666666 transparent transparent  transparent;/* 颜色 */
				_border-color:#666666  #fff #fff  #fff;/* 颜色 */
				_filter:chroma(color=white);
				right:20px;
				top:50%;
				margin-top: -3px;
			}
		}
	}
	.header-select-item{
		margin-top: 13px;
		position:relative;
		width:137px;
		padding:20px 0px 0px;
		background:#fff;
		box-shadow: 1px 1px 5px #000;
		border-radius: 3px;
		.ui-triangle-top-max-white{
		position:absolute;
		left:15px;
		top:-4px;}
		.item-wrap{
			.item{
				.title{
					font-size: 14px;
					color:#000;
					padding:0px 18px 5px;
				}
				.list{
					li{
						height:34px;
						line-height: 34px;
						font-size: 14px;
						a{
							padding:0px 18px;
							color:#666666;
							text-decoration: none;
							height:34px;
							display:block;
							&:hover{
								background:#eeeeee;
							}
						}

					}
					.cur a{
						background:#eeeeee;
					}	
				}
				.list-1{
					li{
						padding:0px 18px;
						color:#4092f1;
					}
				}

			}
		}
	}
}